import React from "react";
import { Button, Empty, Form, Input, Select } from "antd";
import { injectIntl } from "react-intl";
import Context from "../../../Context";

const { Option } = Select;

class OcppCommandsForm extends React.Component {
  static contextType = Context;

  mc = (key, prefix) => {
    return this.props.m(this.props.command + "." + key, prefix);
  };

  m = (key, prefix) => {
    return this.props.m(key, prefix);
  };

  render() {
    if (!this.props.station || !this.props.command) return <Empty />;

    const command = this.context.ocpp.commands[this.props.command];
    const form = command?.form;

    if (!form) return <Empty />;

    const renderItems = (form) => {
      const { properties, required } = form;
      if (Object.entries(properties).length === 0)
        return (
          <div className="gx-text-grey gx-font-weight-normal gx-mb-3">
            {this.m("noform")}
          </div>
        );
      return Object.entries(properties).map(([key, item]) => {
        return (
          <Form.Item
            key={key}
            rules={[{ required: required?.includes(key) }]}
            label={this.mc(key)}
            name={key}
            extra={
              <div className="gx-text-grey gx-font-weight-light gx-text-small">
                <span className="gx-font-weight-normal gx-mr-2">
                  {this.m("type." + item.type)}
                </span>
                {this.mc(key + ".description")}
              </div>
            }
          >
            {renderInput(item)}
          </Form.Item>
        );
      });
    };

    const renderInput = (item) => {
      switch (item.type) {
        case "string":
          if (item.enum) {
            return (
              <Select>
                {item.enum.map((v) => (
                  <Option key={v} value={v}>
                    {v}
                  </Option>
                ))}
              </Select>
            );
          }
          if (item.format === "date-time") {
            return <Input />;
          }
          return <Input />;
        case "array":
          return <Input />;
        case "object":
          return <Input />;
        case "integer":
          return <Input type={"number"} />;
        case "number":
          return <Input type={"number"} />;
        default:
          return null;
      }
    };

    return (
      <>
        <Form
          layout="vertical"
          name="command"
          onFinish={this.props.onFinish}
          key={this.props.command}
          scrollToFirstError
        >
          {renderItems(form)}
          <Button type="primary" htmlType="submit">
            {this.m("submit", "")}
          </Button>
        </Form>
      </>
    );
  }
}
export default injectIntl(OcppCommandsForm);
